       url('/assets/Lars-Light.woff2') format('woff2'),
       url('/assets/Lars-Light.woff') format('woff');
}
@font-face {
  font-family: 'Lars Mono';
  src: url('/assets/Lars-Mono.eot');
  src: url('/assets/Lars-Mono.eot?#iefix') format('embedded-opentype'),
       url('/assets/Lars-Mono.woff2') format('woff2'),
       url('/assets/Lars-Mono.woff') format('woff');
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Lars Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Lars Sans';
  src: url(/jodi-app/static/media/Lars-Light.17b75bb4.eot);
  src: url(/jodi-app/static/media/Lars-Light.17b75bb4.eot?#iefix) format('embedded-opentype'),
       url(/jodi-app/static/media/Lars-Light.fe257c4d.woff2) format('woff2'),
       url(/jodi-app/static/media/Lars-Light.7577ad03.woff) format('woff');
}
@font-face {
  font-family: 'Lars Mono';
  src: url(/jodi-app/static/media/Lars-Mono.703f6f95.eot);
  src: url(/jodi-app/static/media/Lars-Mono.703f6f95.eot?#iefix) format('embedded-opentype'),
       url(/jodi-app/static/media/Lars-Mono.0cd39448.woff2) format('woff2'),
       url(/jodi-app/static/media/Lars-Mono.eccb076b.woff) format('woff');
}

.App {
  text-align: center;
  font-family: 'Lars Sans', sans-serif !important;
  margin-top: 30px !important;
}

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-font-feature-settings:"kern" 1;
  -o-font-feature-settings:"kern" 1;
  -webkit-font-feature-settings:"kern" 1;
  font-feature-settings:"kern" 1, "kern";
  -webkit-font-kerning: normal;
          font-kerning: normal;
}

h1 {
  font-family: 'Lars Mono', sans-serif !important;
}

h2, h3, h4, h5 {
  font-family: 'Lars Sans', sans-serif !important;
}

a, p {
  font-family: 'Lars Sans', sans-serif !important;
  font-size: 16px !important;
}

.mdl-layout__drawer-button {
  color: grey !important;
}

a {
  color: inherit !important;
}

#header-bar {
  background-color: white;
  position: fixed;
}
#header-bar.dark-mode a{
  color: #ddd !important;
}

#header-bar a:hover {
  text-decoration: underline;
}

#header-bar a:active {
  text-decoration: line-through;
}

#page-container {
  max-width: 1000px;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;
  padding: 1rem;
}

#toggle-light {
  z-index: 3;
  background: none;
  color: inherit;
  border: 0;
  font-size: 1rem;
  position: absolute;
  top: 0;
  line-height: 1;
  right: 0;
  padding: 0.75rem;
  outline: 0;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -moz-user-select: none;
}
svg {
  height: 2rem;
  width: 2rem;
}
svg path {
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  transition: transform 150ms ease-out, opacity 150ms ease-out;
  transition: transform 150ms ease-out, opacity 150ms ease-out, -webkit-transform 150ms ease-out;
}
#toggle-light.light-mode svg path {
  -webkit-transition-delay: 125ms;
          transition-delay: 125ms;
}
#toggle-light.dark-mode svg path {
  opacity: 0;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
#orb {
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  transition: transform 150ms ease-out, opacity 150ms ease-out;
  transition: transform 150ms ease-out, opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  -webkit-transform: translate(30px, 30px);
          transform: translate(30px, 30px);
}
#cut {
  -webkit-transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  transition: opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  transition: transform 150ms ease-out, opacity 150ms ease-out;
  transition: transform 150ms ease-out, opacity 150ms ease-out, -webkit-transform 150ms ease-out;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transform: translate(30px, -30px);
          transform: translate(30px, -30px);
}
#toggle-light.dark-mode #cut {
  -webkit-transform: translate(10px, -10px);
          transform: translate(10px, -10px);
  -webkit-transition-delay: 125ms;
          transition-delay: 125ms;
  fill: rgb(0,0,0);
}
#toggle-light.dark-mode #orb {
  -webkit-transform: scale(1.3) translate(30px, 30px);
          transform: scale(1.3) translate(30px, 30px);
  -webkit-transition-delay: 125ms;
          transition-delay: 125ms;
  fill: white;
}
#toggle-light.light-mode #cut {
  -webkit-transform: translate(10px, -10px);
          transform: translate(10px, -10px);
  -webkit-transition-delay: 125ms;
          transition-delay: 125ms;
  display: none;
}
#toggle-light.light-mode #orb {
  -webkit-transform: scale(1.05) translate(30px, 30px);
          transform: scale(1.05) translate(30px, 30px);
  -webkit-transition-delay: 125ms;
          transition-delay: 125ms;
  fill: black;
}

.light-mode {
  background-color: #fff ;
  color: #111;
  -webkit-transition: background-color 0s ease-out;
  transition: background-color 0s ease-out;
}
.dark-mode {
  background-color: #1a1919 !important;
  color: #eee !important;
  -webkit-transition: background-color 0s ease-out;
  transition: background-color 0s ease-out;
}

.fix-margin p {
  margin: 0;
}
.fix-margin a {
  text-decoration: underline;
}

/* Home Page */
.homepage-banner {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  vertical-align: middle; 
  height: 90vh;
}

/* About Page */
.about-grid-info {
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 auto;
}
.about-grid-info h1 {
  margin: 0;
